@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
*{
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  min-height:100vh;
  width:100%; 
  display: grid;
  grid-template-columns: 1fr 350px;
  font-family: 'Ubuntu', sans-serif;
}

.body{
  /* width: 100%; */
  min-height: calc(100vh - 250px);
  /* background-color: orange; */
  box-sizing: border-box;
  padding:4% 0;  
}


.RightSection{
  /* flex:1; */
  background-color: rgba(189, 0, 255, 0.03);
  display: flex;
  flex-direction: column;
}

.UserData{
  height:200px;
  /* background-color: red; */
  display: flex;
  flex-direction: column;
  background: url(/bookstore/static/media/userdata.efae459d.svg) no-repeat;
  background-size: contain;
}


.Header{
    /* width:100%; */
    height:200px;
    box-sizing: border-box;
    padding:0 20px;
    display: flex;
    justify-content: center;
    align-items: flex-end; 
    /* background-color: red; */
  }
  
  .header-container{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 252px);
    grid-gap: 20px;
    justify-content: center;
    align-items: center;
    /* background-color: orange; */
    
  }
  
  .logo{
    grid-column: 1/3;
    /* background-color: red; */
    display: flex;
    justify-content: start;
  }

  .logo img{
      width: 30%;
  }
    .search-container{
    /* flex:1; */
    position: relative;
    grid-column: 3/5;
    border:1px solid gray;
    height:45px;
    display: flex;
    justify-content: space-between;
    border-radius: 0px 12px 12px 0px;
    align-items: center;
    box-sizing: border-box;
    /* overflow: hidden; */
    background-color: #bd00ff;
    }

    .search-container:hover{
    border: 1px solid #bd00ff;
    }

    .search-container input{
    flex:1 1;
    border:none;
    border-right: 1px solid gray;
    border-radius: 0px 12px 12px 0px;
    outline: none;
    /* background-color: red; */
    height:100%;
    font-size: 18px;
    box-sizing: border-box;
    -webkit-user-select: text;
        -ms-user-select: text;
            user-select: text;
    /* margin:0 10px; */
    }

    .search-container input:hover{
    border-right: 1px solid #bd00ff;
    }

    .filter, .search{
    height:100%;
    padding:0px 5px;
    display: flex;
    align-items: center;
    font-size: 32px;
    color:white;
    }

    .filter{
    background-color: white;
    color:#555555;
    }

.searchBtn, .filterBtn{
    cursor: pointer;
}

.open{
    color: blue;
}

.filterBox{
    position: absolute;
    left:0;
    top:-90px;
    z-index:2;
    width:100%;
    height:70px;
    background-color: white;
    border:3px solid #bd00ff;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 1fr 1fr;
    align-items: center;
    box-sizing: border-box;
    padding: 5px;
}

.filterBox input{
    border:none;
    border-bottom:1.5px solid #bd00ff;
    border-radius: 0px;
}

.genreTagNote{
    grid-column: 1/-1;
    font-size: 15px;
    color: #555555;
}


.downArrow{
    z-index:1;
    position: absolute;
    left:8px;
    top:-32px;
    font-size: 32px;
    color: #bd00ff;
}
.book-container{
    width: 100%;
    /* background-color:red; */
    display: grid;
    grid-template-columns: repeat(4, 252px);
    justify-content: center;
    grid-row-gap: 20px;
    grid-column-gap: 20px;
  }
  
  .book{
    border:1px solid #d3d3d3;
    height:426px;
    background-color:#f2f2f2;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position:relative;
  }
  
  .genre{
    position:absolute;
    left:1px;
    top:1px;
    padding:1%;
    background-color: white;  
  }
  
  .image{
    width:250px;
    height:250px;
    align-self: center;
    background-color: green;
  }
  
  .title{
    margin-top:5px;
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: 600;
  }
  
  .book span{
    /* width:100%; */
    box-sizing: border-box;
    text-align: left;
    padding: 5px 10px;
  }

  .stockOut{
    color: red;
  }
  
  .priceAndCart{
    display: flex;
    justify-content: space-between;
    width:100%;
    box-sizing: border-box;
    padding-right:10px;
  }
  
  .price{
    font-weight: bold;
    color:green;
  }
  
  .priceAndCart .addToCart{
    cursor: pointer;
    font-size: 24px;
    color: #bd00ff;
  }

  .disabled{
    color: #aaaaaa;
    font-size: 24px;
  }
.Cart{
margin-top: 25px;
border-left:4px solid rgba(189, 0, 255, 0.31);
flex:1 1;
padding: 25px 10px;
display: flex;
flex-direction: column;
align-items: flex-start;
}
  
.mycart{
display: flex;
font-size: 24px;
font-weight: bold;
margin-bottom: 15px;
}

.mycart img{
margin-right: 10px;
}

.selected-book-container{
display: flex;
flex-direction: column;
box-sizing: border-box;
width: 100%;
margin:15px 0px;
}

.selected-book{
display: grid;
box-sizing: border-box;
margin:5px 0px;
grid-template-columns: 64px 1fr 1fr 1fr;
grid-template-rows: 32px 32px;
grid-gap: 5px;
align-items: center;
width: 100%;
background-color: #f2f2f2;
text-align: justify;

}

.selectedbookImage{
grid-row: 1/-1;
width:64px;
height:64px;
background-color: orange;
}

.selectedbookImage img{
    width: 100%;
}

.selectedTitle{
grid-column: 2/4;
/* background-color: red; */
font-size: 15px;
font-weight: 600;
}

.closeDiv{
box-sizing: border-box;
display: flex;
padding:5px;
justify-content: flex-end;
}

.closeBtn{
font-size: 24px;
color: red;
}

.selectedAuthor{
font-size: 15px;
}

.counter{
display: flex;
justify-content: space-around;
align-items: center;
}

.decBtn, .incBtn{
color: #bd00ff;
cursor: pointer;
}

.selectedPrice{
/* background-color: red; */
font-weight: bold;
color:green;
}

